.profile-contact-container {
  display: flex;
  justify-content: center; /* Đẩy profile contact ra giữa theo chiều ngang */
  align-items: center; /* Đẩy profile contact ra giữa theo chiều dọc */
  margin-right: 110px;
  min-height: 90vh; /* Chiều cao tối thiểu của toàn bộ trang */
}

.profile-contact {
  text-align: center;
  max-width: 800px; /* Đảm bảo rằng profile contact không quá rộng */
  font-family: 'Comic Sans MS', sans-serif;
}

.contact-grid {
  display: flex;
  justify-content: space-between; /* Phân chia không gian đều giữa các cột */
}

.contact-column {
  flex: 0 0 100%; /* Chiều rộng cố định của mỗi cột */
  display: flex;
  flex-direction: column;
}

.contact-column:first-child {
  align-self: flex-start; /* Căn lề trái cho cột đầu tiên */
}

.contact-column:last-child {
  align-self: flex-end; /* Căn lề trái cho cột cuối cùng */
}

.contact-item {
  display: flex;
  align-items: center; /* Căn chữ ngang hàng với icon theo chiều dọc */
  text-decoration: none; /* Loại bỏ gạch chân */
  color: black; /* Đổi màu văn bản thành màu đen */
  margin-bottom: 16px;
}

.contact-icon {
  font-size: 46px;
}

.contact-text {
  margin-left: 20px; /* Khoảng cách giữa icon và text */
  font-size: 26px; /* Kích thước font của văn bản mô tả */
}
